* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(
    to right bottom,
    rgb(11, 212, 169) 100%,
    rgba(0, 212, 255, 1) 100%
  );
  max-height: 100vh;
  width: 100%;
}
.wrap {
  background-color: rgba(44, 44, 44, 0.37);
  height: 100vh;
  padding: 0 2rem;
  max-width: 700px;
  margin: 0 auto;
}
.fas {
  font-size: 1.3rem;
  padding: 3px;
  cursor: pointer;
  transition: all 0.2 ease-in-out;
}
.fas:hover {
  color: #dc3545;
  transform: translateY(-2px);
}
li {
  font-weight: bold;
  font-size: 1.2rem;
  text-transform: uppercase;
}
.list-group {
  background-color: rgba(47, 45, 45, 0.589);
  margin-bottom: 10px;
  height: 300px;

  overflow-y: scroll;
}
/* .list-group-item {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  padding-right: 17px;
  box-sizing: content-box;
} */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
